import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ClimasPage } from './climas.page';

const routes: Routes = [
  {
    path: '',
    component: ClimasPage
  },
  {
    path: 'ws-clim',
    loadChildren: () => import('../../Pages/climas/ws-clim/ws-clim.module').then( m => m.WsClimPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ClimasPageRoutingModule {}
